.HobbiesSection {
  .hobbies {
    width: 95%;
    background-color: #111111;
    border-left: 1px solid #ff000049;
    border-right: 1px solid #ff000049;
    border-bottom: 1px solid #ff000049;
    border-top: 8px solid #ff000049;
    transition: all 0.4s ease-in-out;

    @media screen and (max-width:1400px){
      width: 100%;
      margin-bottom: 1rem;
  }

    &:hover {
      border-top: 8px solid red;
    }

    .hobbies-content {
      padding: 1rem;

      .h-title {
        font-size: 1.6rem;
        font-weight: 500;
        position: relative;
        padding-bottom: 1rem;
        margin: 1rem 0;
        &::after {
          content: "";
          position: absolute;
          bottom: 0;
          left: 0;
          width: 30%;
          height: 1px;
          background-color: #ff000049;
        }
      }

      .h-image {
        max-width: 300px;
        max-height: 300px;
      }
    }
  }
}

.hobbies-container {
  display: flex;
  justify-content: space-between;
  margin-top: 4rem;

  @media screen and (max-width: 1400px) {
    flex-direction: column;
    margin-bottom: 2rem;
  }
}
