.HomePage {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hero-text {
  color: white;
  text-align: center;
  font-size: 3.6rem;
  span {
    color: #ff0303;
  }
}

.h-sub-text {
  text-align: center;
}

.icons {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.5rem 0rem;

  .icon-holder {
    margin-right: 1rem;

    .icon {
      font-size: 2.8rem;
      color: #ffffff;
      transition: all .4s ease-in-out;
    }

    .fb:hover {
      color: #3b5998;
    }

    .ig:hover {
      color: #c13584;
    }
    .gh:hover {
      color: #bd2c00;
    }
    .in:hover {
      color: #00a0dc;
    }
  }
}
